import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { push } from 'connected-react-router'
import { AppThunk } from 'store'
import { ApiAction, ApiMiddlewarePayload } from 'types'
import { SessionUtils } from 'utils'
import { createApiAction } from 'middlewares/apiActionHelper'
import { keepSessionAlive } from 'core/session/api/SessionService'
import { SessionState } from 'core/session/types'
import { userLoggedOut } from 'modules/Auth/slices/auth'
import authRoutes from 'modules/Auth/routing/routes'

const initialState: SessionState = {
  keepAlive: {},
}

const sessionSlice = createSlice({
  name: 'session',
  initialState,
  reducers: {
    refreshTokenDuration(state, action: PayloadAction<ApiMiddlewarePayload>) {
      state.keepAlive = action.payload
    },
  },
  extraReducers: builder => {
    builder.addCase(userLoggedOut, () => {
      return initialState
    })
  },
})

export const { refreshTokenDuration } = sessionSlice.actions
export default sessionSlice.reducer

export const handleUserAuthentification = (
  response: ApiMiddlewarePayload
): AppThunk => async dispatch => {
  const { error } = response
  // TODO: Define implementation based on error code
  if (error) {
    SessionUtils.clearSession()
    dispatch(push(authRoutes.login))
  }
  dispatch(refreshTokenDuration(response))
}

export const keepTokenAlive = (): ApiAction => {
  return createApiAction({
    apiCalls: {
      keepAlive: () => keepSessionAlive(),
    },
    payloadAction: refreshTokenDuration,
  })
}
