import React, { Suspense } from 'react'
import { Route, Switch } from 'react-router-dom'
import { ConnectedRouter } from 'connected-react-router'
import { history } from 'store'
import { RouteChangeProgress } from 'components/common'
import authRoutes from 'modules/Auth/routing/routes'
import {
  LoginRoute,
  ForgotPasswordRoute,
  SetupPasswordRoute,
} from 'modules/Auth/routing'
import { PublicRoute } from './components'
import PrivateRouting from './PrivateRouting'
import rootRoutes from './routes'

const RootRouting = () => {
  return (
    <ConnectedRouter history={history}>
      <Suspense fallback={<RouteChangeProgress />}>
        <Switch>
          <PublicRoute
            path={authRoutes.login}
            component={LoginRoute}
            restricted
          />
          <PublicRoute
            path={authRoutes.forgot}
            component={ForgotPasswordRoute}
            restricted
          />
          <PublicRoute
            path={authRoutes.setup}
            component={SetupPasswordRoute}
            restricted
          />
          <Route path={rootRoutes.root} component={PrivateRouting} />
        </Switch>
      </Suspense>
    </ConnectedRouter>
  )
}

export default RootRouting
