import styled, { css } from 'styled-components'
import { media } from 'styled-bootstrap-grid'
import { DecoratorProps, Variant } from './Decorator'
import bgDesktopAuth from 'assets/images/AuthLayout/background_desktop.jpg'
import bgMobileAuth from 'assets/images/AuthLayout/background_mobile.jpg'
import bgDesktopTrainingLibrary from 'assets/images/TrainingLibrary/background_desktop.jpg'

const backgroundImageSettings = css<DecoratorProps>`
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
  background-attachment: ${({ wrapper }) => (wrapper ? 'scroll' : 'fixed')};
`

const authLayoutBackground = css`
  background-image: url(${bgMobileAuth});

  ${media.sm`
    background-image: url(${bgDesktopAuth});
  `}

  ${backgroundImageSettings}
`

const traningLibraryBackground = css`
  ${({ theme }) => `
    background-color: ${theme.color.ocean};
    ${theme.pattern};
  `}

  ${media.sm`
    background-image: url(${bgDesktopTrainingLibrary});
    ${backgroundImageSettings}
  `}
`

export const Wrapper = styled.div<DecoratorProps>`
  height: ${({ wrapper }) => (wrapper ? 'auto' : '100%')};
  background-color: ${({ theme, color }) =>
    color ? theme.color[color] : 'transparent'};

  ${({ theme, variant }) => {
    switch (variant) {
      case Variant.Pattern:
        return theme.pattern
      case Variant.AuthLayout:
        return authLayoutBackground
      case Variant.TraningLibrary:
        return traningLibraryBackground

      default:
        return null
    }
  }};
`
