import { Method, ApiResponse, ErrorResponse } from 'types'
import { SessionUtils } from 'utils'

const ACCEPT_HEADER_NAME = 'Accept'
const ACCEPT_HEADER_VALUE = 'application/json'
const CONTENT_TYPE_HEADER_NAME = 'Content-Type'
const CONTENT_TYPE_HEADER_VALUE = 'application/json; charset=utf-8'
const SESSION_TOKEN_HEADER = 'Ocean-SessionToken'
const API_PREFIX = '/olp/api/ocean/v1'

const callApi = async (
  path: string,
  method: Method,
  params: any = null
): Promise<ApiResponse> => {
  const address = `${API_PREFIX}${path}`
  try {
    const response = await fetch(address, {
      method,
      headers: {
        [ACCEPT_HEADER_NAME]: ACCEPT_HEADER_VALUE,
        [CONTENT_TYPE_HEADER_NAME]: CONTENT_TYPE_HEADER_VALUE,
        [SESSION_TOKEN_HEADER]: SessionUtils.getSessionToken() || '',
      },
      body: params ? JSON.stringify(params) : null,
    })

    const responseError = getResponseError(response)
    if (responseError) {
      return createErrorResponse(responseError)
    }

    const { ok, status } = response
    const parsedResponse = await response.json()
    return createSuccessResponse(ok, status, parsedResponse)
  } catch (error) {
    return createErrorResponse(error)
  }
}

export default callApi

const getResponseError = (response: Response) => {
  const { ok, status, statusText } = response
  const contentType = response.headers.get('content-type')
  const isJsonResponse = contentType && contentType.includes('application/json')

  if (isJsonResponse) return // if JSON we can continue

  if (!ok) {
    // !ok - status not in (200-299) and we can't continue
    return new Error(`Status: ${status}, StatusText: ${statusText}`)
  }

  return new TypeError('No JSON in response')
}

const createSuccessResponse = (ok: boolean, status: number, response: any) => ({
  success: ok,
  status: status,
  content: ok ? response : undefined,
  error: !ok ? normalizeError(status, response) : undefined,
})

const normalizeError = (status: number, response: any): ErrorResponse => {
  let error: any = response
  if (typeof response === 'string') {
    error = { code: status, error: response }
  } else if (response.message) {
    error = { code: status, error: response.message }
  }
  return error as ErrorResponse
}

const createErrorResponse = (error: any) => ({
  success: false,
  error: {
    code: 'ApiInternalError',
    details: error instanceof Error ? error.message : null,
    error: 'Unexpected error during executing or parsing api request response',
  },
})
