import React, { Suspense } from 'react'
import { Switch, Redirect } from 'react-router-dom'
import { RouteChangeProgress } from 'components/common'
import { AppLayout } from 'layouts'
import { AppLayoutSegmentProps } from 'layouts/types'
import { PrivateRoute } from './components'
import { SessionLayer } from 'core/session/components'
import { HomeRoute } from 'modules/Home/routing'
import homeRoutes from 'modules/Home/routing/routes'
import {
  TrainingLibraryRoute,
  TrainingLibraryModulesRoute,
} from 'modules/TrainingLibrary/routing'
import trainingLibraryRoutes from 'modules/TrainingLibrary/routing/routes'

const Content: React.FC<AppLayoutSegmentProps> = () => (
  <Suspense fallback={<RouteChangeProgress />}>
    <Switch>
      <PrivateRoute exact path={homeRoutes.home} component={HomeRoute} />

      <PrivateRoute
        exact
        path={trainingLibraryRoutes.trainingLibrary}
        component={TrainingLibraryRoute}
      />
      <PrivateRoute
        exact
        path={`${trainingLibraryRoutes.modules}/:categoryId`}
        component={TrainingLibraryModulesRoute}
      />

      <Redirect path='*' to={homeRoutes.home} />
    </Switch>
  </Suspense>
)

const Sidebar: React.FC<AppLayoutSegmentProps> = () => <div />

const Mobilebar: React.FC<AppLayoutSegmentProps> = () => <div />

const PrivateRouting: React.FC = () => {
  return (
    <SessionLayer>
      <AppLayout>
        <Sidebar segmentId='sidebar' />
        <Content segmentId='content' />
        <Mobilebar segmentId='mobilebar' />
      </AppLayout>
    </SessionLayer>
  )
}

export default PrivateRouting
