import React from 'react'
import { Color } from 'theme/types'
import { Wrapper } from './Decorator.styled'

export enum Variant {
  AuthLayout,
  TraningLibrary,
  Pattern,
}

export type DecoratorProps = {
  color?: Color
  wrapper?: boolean
  variant?: Variant
}

const Decorator: React.FC<DecoratorProps> = ({
  children,
  color,
  variant,
  wrapper,
}) => (
  <Wrapper color={color} variant={variant} wrapper={wrapper}>
    {children}
  </Wrapper>
)

export default Decorator
