import React from 'react'
import { LayoutProps } from '../types'
import withLayout from '../withLayout'
import { Root, Header, Content } from './PageLayout.styled'

export type PageLayoutSegments = 'header' | 'content'
type InternalLayoutProps = LayoutProps<PageLayoutSegments>

const PageLayout = ({ segments }: InternalLayoutProps) => {
  return (
    <Root>
      {segments?.header && <Header>{segments.header}</Header>}
      <Content>{segments?.content}</Content>
    </Root>
  )
}

export default withLayout(PageLayout)
