export const OCEAN_SESSION_TOKEN_NAME = 'SG-Ocean-Session-Token'

class SessionUtils {
  storeSession(token: string) {
    localStorage.setItem(OCEAN_SESSION_TOKEN_NAME, token)
  }

  getSessionToken() {
    return localStorage.getItem(OCEAN_SESSION_TOKEN_NAME)
  }

  clearSession() {
    localStorage.removeItem(OCEAN_SESSION_TOKEN_NAME)
  }
}

export default new SessionUtils()
