import { RootState } from 'store'

export type Method = 'GET' | 'POST' | 'PUT' | 'DELETE' | 'PATH'

export type ErrorResponse = {
  code?: string
  error: string
  details?: any
}

export type ApiResponse<T = any> = {
  status?: number
  success: boolean
  content?: T
  error?: ErrorResponse
}

export enum ApiActionMode {
  Default,
  ManualProcessing,
}

export type MiddlewareApiAction = {
  type: string
} & ApiAction

export type ApiAction = {
  apiCalls: ApiCalls
  payloadAction: any
  nextAction?: any
  shouldCallApi?: (state: RootState) => boolean
  actionMode?: ApiActionMode
}

export type ApiCall = () => Promise<ApiResponse>

export type ApiCalls = {
  [key: string]: ApiCall
}

export type FullPayload = {
  [key: string]: ApiResponse
}

export type ApiMiddlewarePayload<T = any> = {
  isLoading?: boolean
  error?: any
  result?: T
}
