import getConfigValue from 'config/clientCofigurtaionProvider'
import callApi from './api'

export const usingGet = (path: string) => {
  return callApi(createApiEndpoint(path), 'GET')
}

export const usingPost = (path: string, params: any) => {
  return callApi(createApiEndpoint(path), 'POST', params)
}

export const usingPut = (path: string, params?: any) => {
  return callApi(createApiEndpoint(path), 'PUT', params)
}

export const usingDelete = (path: string, params?: any) => {
  return callApi(createApiEndpoint(path), 'DELETE', params)
}

const createApiEndpoint = (address: string) => {
  const apiBaseAddress = getConfigValue('apiBaseAddress')
  return `${apiBaseAddress}${address}`
}
