import { MiddlewareAPI, Dispatch } from 'redux'
import {
  MiddlewareApiAction,
  FullPayload,
  ApiCall,
  ApiActionMode,
  ApiResponse,
} from 'types'

type ActionHandlerInput = {
  apiCallsResults: ApiResponse[]
  apiCallKeys: string[]
  next: Dispatch
  dispatch: Dispatch
  apiAction: MiddlewareApiAction
}

type DefaultOut = {
  isLoading: boolean
  result?: any
  error: any
}

const DEFAULT_PAYLOAD: DefaultOut = {
  isLoading: false,
  error: null,
}

const apiMiddleware = ({ dispatch, getState }: MiddlewareAPI) => (
  next: Dispatch
) => async (action: MiddlewareApiAction) => {
  if (action.type !== 'OCEAN_API_ACTION') {
    return next(action)
  }

  // Mechanism for caching || preventing duplicate requests
  if (action.shouldCallApi && !action.shouldCallApi(getState())) {
    return
  }

  const apiAction = action as MiddlewareApiAction
  const { apiCalls, actionMode } = apiAction

  dispatch(apiAction.payloadAction({ ...DEFAULT_PAYLOAD, isLoading: true }))

  const apiCallKeys = Object.keys(apiCalls)
  const apiCallValues = Object.values(apiCalls)
  const apiCallsResults = await Promise.all([
    ...apiCallValues.map((singleCall: ApiCall) => singleCall()),
  ])

  const apiActionMode = actionMode || ApiActionMode.Default
  const actionHandlerInput: ActionHandlerInput = {
    apiCallsResults,
    apiCallKeys,
    next,
    dispatch,
    apiAction,
  }
  apiActionModeHandlers[apiActionMode](actionHandlerInput)
}

const runInDefaultMode = (actionHandlerInput: ActionHandlerInput) => {
  const { apiCallsResults, apiCallKeys, next, apiAction } = actionHandlerInput
  const { payloadAction, nextAction } = apiAction

  const defaultOut: DefaultOut = {
    ...DEFAULT_PAYLOAD,
  }

  // Check error
  const firstFailed = apiCallsResults.find(response => !response.success)
  if (firstFailed) {
    defaultOut.error = firstFailed.error
  } else {
    defaultOut.result = createPayload(
      apiCallKeys,
      apiCallsResults.map((result: ApiResponse) => result.content)
    )
  }

  // if (status === 401) {
  //   // TODO: dispatch unauthorized
  // }

  const actionProcess = nextAction || payloadAction

  next(actionProcess(defaultOut))
}

// TODO: Need to check and make adjustments
const runInManualProcessingMode = (actionHandlerInput: ActionHandlerInput) => {
  const { apiCallsResults, apiCallKeys, next, apiAction } = actionHandlerInput
  const payload = createPayload(apiCallKeys, apiCallsResults)
  next(apiAction.nextAction(payload))
}

const createPayload = (
  apiCallKeys: string[],
  apiCallsResults: ApiResponse<any>[]
) => {
  const fullPayload: FullPayload = {}
  apiCallKeys.forEach((element, index) => {
    fullPayload[element] = apiCallsResults[index]
  })
  return fullPayload
}

const apiActionModeHandlers = {
  [ApiActionMode.Default]: runInDefaultMode,
  [ApiActionMode.ManualProcessing]: runInManualProcessingMode,
}

export default apiMiddleware
