import React from 'react'
import { useBreakpoint } from 'hooks'
import { LayoutProps } from '../types'
import withLayout from '../withLayout'
import {
  Root,
  Content,
  Sidebar,
  SidebarInsides,
  Mobilebar,
} from './AppLayout.styled'

export type AppLayoutSegments = 'sidebar' | 'content' | 'mobilebar'
type InternalLayoutProps = LayoutProps<AppLayoutSegments>

const AppLayout = ({ segments }: InternalLayoutProps) => {
  const isDesktop = useBreakpoint('sm')

  return (
    <Root>
      {isDesktop && (
        <Sidebar>
          <SidebarInsides>{segments?.sidebar}</SidebarInsides>
        </Sidebar>
      )}
      <Content>{segments?.content}</Content>
      {!isDesktop && <Mobilebar>{segments?.mobilebar}</Mobilebar>}
    </Root>
  )
}

export default withLayout(AppLayout)
