import { createSlice, PayloadAction, createSelector } from '@reduxjs/toolkit'
import { AppThunk } from 'store'
import { SortUtils, NormalizeUtils } from 'utils'
import { ApiAction, ApiMiddlewarePayload } from 'types'
import { createApiAction } from 'middlewares/apiActionHelper'
import { userLoggedOut } from 'modules/Auth/slices/auth'
import {
  getTrainingCategories,
  getTrainingModules,
} from 'modules/TrainingLibrary/api/trainingLibraryService'
import {
  ApiTrainingLibraryDataResponse,
  TrainingLibraryData,
  TrainingLibraryState,
  TrainingCategoriesById,
  TrainingModulesById,
} from 'modules/TrainingLibrary/types'

const initialState: TrainingLibraryState = {
  trainingLibraryData: {},
}

const mainSlice = createSlice({
  name: 'trainingLibrary',
  initialState,
  reducers: {
    trainingLibraryDataLoaded(
      state,
      action: PayloadAction<ApiMiddlewarePayload<TrainingLibraryData>>
    ) {
      state.trainingLibraryData = action.payload
    },
  },
  extraReducers: builder => {
    builder.addCase(userLoggedOut, () => {
      return initialState
    })
  },
})

const { trainingLibraryDataLoaded } = mainSlice.actions
export default mainSlice.reducer

// return categories array
export const getCategories = createSelector(
  (state: TrainingLibraryData | undefined) => state,
  state => state && Object.values(state.categoriesById)
)

// return category modules for specific categoryId
export const getModulesForCategory = createSelector(
  (state: TrainingLibraryData | undefined) => state,
  (_: any, categoryId: string) => categoryId,
  (state, categoryId) => {
    if (state) {
      const { categoriesById, modulesById } = state
      // need "?" after categoriesById[categoryId] because categoryId any data from user
      return categoriesById[categoryId]?.moduleIds?.map(
        moduleId => modulesById[moduleId]
      )
    }
  }
)

const handleTrainingLibraryResponse = (
  response: ApiMiddlewarePayload<ApiTrainingLibraryDataResponse>
): AppThunk => async dispatch => {
  const { result, error, isLoading } = response

  if (error || !result) {
    dispatch(trainingLibraryDataLoaded({ error, isLoading }))
    return
  }

  const { categories } = result.categories
  const { modules } = result.modules

  // create modulesIds
  categories.forEach(category => {
    category.moduleIds = modules
      .filter(module => module.categoryId === category.id)
      .map(module => module.id)
  })

  // normalize modules - it's sorted by the backend already
  const modulesById = NormalizeUtils.arrayToObj<TrainingModulesById>(modules)

  // normalize categories
  categories.sort(SortUtils.sortNumeric('sortCode'))
  const categoriesById = NormalizeUtils.arrayToObj<TrainingCategoriesById>(
    categories
  )

  dispatch(
    trainingLibraryDataLoaded({
      ...response,
      result: { categoriesById, modulesById },
    })
  )
}

export const getTrainingLibraryData = (): ApiAction => {
  return createApiAction({
    apiCalls: {
      categories: () => getTrainingCategories(),
      modules: () => getTrainingModules(),
    },
    payloadAction: trainingLibraryDataLoaded,
    nextAction: handleTrainingLibraryResponse,
  })
}
