import React, { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { keepTokenAlive } from 'core/session/slices/session'

type SessionLayerProps = {
  children: React.ReactNode
}

const SESSION_ALIVE_INTERVAL: number = 30000

const SessionLayer: React.FC<SessionLayerProps> = ({ children }) => {
  const dispatch = useDispatch()

  // Keep session alive
  useEffect(() => {
    const keepAliveInterval = setInterval(
      () => dispatch(keepTokenAlive()),
      SESSION_ALIVE_INTERVAL
    )
    return () => clearInterval(keepAliveInterval)
  }, [dispatch])

  return <>{children}</>
}

export default SessionLayer
