import React from 'react'
import { Wrapper, Header, Description } from './Note.styled'

type NoteProps = {
  title: string
  description?: string
}

const Note: React.FC<NoteProps> = ({ title, description }) => {
  return (
    <Wrapper>
      <Header>{title}</Header>
      {description && <Description>{description}</Description>}
    </Wrapper>
  )
}

export default Note
