import { usingPost, usingDelete } from 'api/apiHelpers'
import {
  AuthUserParams,
  ForgotPasswordParams,
  SetupPasswordParams,
} from 'modules/Auth/types'
import { SessionUtils } from 'utils'

export const login = async (authUserParams: AuthUserParams) => {
  return await usingPost('Session/Authentication', authUserParams)
}

export const forgotPassword = async (
  forgotPasswordParams: ForgotPasswordParams
) => {
  // TODO: Fix correct forgot password url
  return await usingPost('Session/ForgotPassword', forgotPasswordParams)
}

export const setupPassword = async (
  setupPasswordParams: SetupPasswordParams
) => {
  // TODO: Fix correct setup password url
  return await usingPost('Session/SetupPassword', setupPasswordParams)
}

export const logout = async () => {
  const sessionToken = SessionUtils.getSessionToken()
  return await usingDelete(`Session/Logout/${sessionToken}`)
}
