import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { push } from 'connected-react-router'
import { AppThunk } from 'store'
import { SessionUtils } from 'utils'
import { ApiAction, ApiMiddlewarePayload } from 'types'
import { createApiAction } from 'middlewares/apiActionHelper'
import {
  login,
  logout,
  forgotPassword,
  setupPassword,
} from 'modules/Auth/api/authService'
import authRoutes from 'modules/Auth/routing/routes'
import homeRoutes from 'modules/Home/routing/routes'
import {
  AuthUserParams,
  ForgotPasswordParams,
  SetupPasswordParams,
  AuthState,
  AuthentificationResponse,
} from 'modules/Auth/types'

const initialState: AuthState = {
  session: {},
  forgotPasswordRequest: {},
  setupPasswordRequest: {},
}

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    userAuthenticated(
      state,
      action: PayloadAction<ApiMiddlewarePayload<AuthentificationResponse>>
    ) {
      state.session = action.payload
    },
    userForgotPasswordRequested(
      state,
      // TODO: Set forgot password response type
      action: PayloadAction<ApiMiddlewarePayload>
    ) {
      state.forgotPasswordRequest = action.payload
    },
    userSetupPasswordRequested(
      state,
      // TODO: Setup password response type
      action: PayloadAction<ApiMiddlewarePayload>
    ) {
      state.setupPasswordRequest = action.payload
    },
    userLoggedOut() {
      return initialState
    },
  },
})

export const {
  userAuthenticated,
  userForgotPasswordRequested,
  userSetupPasswordRequested,
  userLoggedOut,
} = authSlice.actions
export default authSlice.reducer

const handleUserAuthentification = (
  response: ApiMiddlewarePayload<AuthentificationResponse>
): AppThunk => async dispatch => {
  const { error, result } = response
  if (!error && result?.login) {
    const { login } = result
    SessionUtils.storeSession(login.sessionToken)
    dispatch(push(homeRoutes.home))
  }
  dispatch(userAuthenticated(response))
}

export const authenticateUser = (loginState: AuthUserParams): ApiAction => {
  return createApiAction({
    apiCalls: {
      login: () => login(loginState),
    },
    payloadAction: userAuthenticated,
    nextAction: handleUserAuthentification,
  })
}

export const forgotPasswordRequest = (
  forgotPasswordState: ForgotPasswordParams
) => {
  return createApiAction({
    apiCalls: {
      forgot: () => forgotPassword(forgotPasswordState),
    },
    payloadAction: userForgotPasswordRequested,
  })
}

export const setupPasswordRequest = (
  setupPasswordState: SetupPasswordParams
) => {
  return createApiAction({
    apiCalls: {
      setup: () => setupPassword(setupPasswordState),
    },
    payloadAction: userSetupPasswordRequested,
  })
}

export const logoutUser = (): AppThunk => dispatch => {
  try {
    logout()
  } finally {
    SessionUtils.clearSession()
    dispatch(userLoggedOut())
    dispatch(push(authRoutes.login))
  }
}
